body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



  @font-face {
    font-family: "JUSTSansRegular";
    src: local("JUSTSansRegular"),
     url("./fonts/JUSTSans/JUSTSansRegular.otf") format("opentype");
    font-weight: normal;
    }

    @font-face {
      font-family: "DS-DIGI";
      src: local("DS-DIGI"),
       url("./fonts/DS-DIGI.TTF") format("truetype");
      font-weight: normal;
      }
   

      @font-face {
        font-family: "Orpheus-Pro-Italic";
        src: local("Orpheus-Pro-Italic"),
         url('./fonts/Orpheus-Pro-Italic.otf') format("opentype");
        font-weight: italic;
        }
     