* {padding: 0px;
   margin: 0px;
   color: #f5f6fa;
   font-size: 30px;
   font-family: "JUSTSansRegular";}

#main {width: 100vw;
       height: 100vh;
       display: flex;
       justify-content: center;
       align-items: center;
       background-color: #82ccdd;}

#container {display: grid;
            grid-auto-rows: 70px;
            grid-auto-columns: 85px;
            border: 8px solid #303952;
            box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;}
            

#display {grid-row-start: 1;
          grid-column-start: 1;
          grid-column-end: 5;
          background-color: #dcdde1;
          text-align: right;
          padding-right: 5px;
          border: 2px solid #f5f6fa;
          font-family: "DS-DIGI";
          padding-top: 35px;
          color: #0c2461;}

#clear {grid-row-start: 2;
        grid-column-start: 1;
        grid-column-end: 3;
        background-color: #b71540;
        border: 1px solid #f5f6fa;
        display: flex;
        align-items: center;
        justify-content: center;
        border-left: 2px solid #f5f6fa;}

#divide {grid-row-start: 2;
         grid-column-start: 3;
         grid-column-end: 3;
         background-color: #487eb0;
         border: 1px solid #f5f6fa;
         display: flex;
         align-items: center;
         justify-content: center;}

#multiply {grid-row-start: 2;
          grid-column-start: 4;
          grid-column-end: 4;
          background-color: #487eb0;
          border: 1px solid #f5f6fa;
          display: flex;
          align-items: center;
          justify-content: center;
          border-right: 2px solid #f5f6fa;}

#seven {grid-row-start: 3;
        grid-column-start: 1;
        grid-column-end: 1;
        background-color: #273c75;
        border: 1px solid #f5f6fa;
        display: flex;
        align-items: center;
        justify-content: center;
        border-left: 2px solid #f5f6fa;}

#eight {grid-row-start: 3;
        grid-column-start: 2;
        grid-column-end: 2;
        background-color: #273c75;
        border: 1px solid #f5f6fa;
        display: flex;
        align-items: center;
        justify-content: center;}

#nine {grid-row-start: 3;
          grid-column-start: 3;
          grid-column-end: 3;
          background-color: #273c75;
