         align-items: center;
         justify-content: center;
         border-bottom: 2px solid #f5f6fa;
         border-right:  2px solid #f5f6fa;}


#output {font-family: "DS-DIGI";
         font-size: 20px;
         grid-row-start: 1;
         grid-column-start: 1;
         grid-column-end: 5;
         z-index: 1;
         padding-top: 5px;
         text-align: right;
         padding-right: 8px;
         color: #0c2461}

